$purple: #CC99FF;
$light-blue: #BEFFFF;
$light-orange: #FFCC99;
$light-green: #CCFF99;
$gray: #787878;
$charcoal: #404040;
$light-gray: #CCCCCC;
$white: #FFF;
$light-blue-hover: #8ddde3;
$light-green-hover: #a2e594;
$background: rgba(0,0,0,0.4);
$lavender: #9999FF;
$pink: #FF9999;
$grass: #bafbba;

body {
  background-image: radial-gradient(rgb(153, 153, 153) 10%, transparent 10%);
  background-position: 10px 10px;
  background-size: 18px 18px;
  background-color: rgb(255, 255, 255);
}

* {
  border-radius: 5px;
  font-family: Poppins, sans-serif;
  color: $charcoal !important;
}

.board {
  border: 1px solid $gray;
  width: 1100px;
  margin: 30px auto;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  width: 1098px;
  height: 100px;
  background-color: $purple;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 32px;
}

.time {
  background-color: $white;
  border: 1px solid $gray;
  font-size: 18px;
  width: 80px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.todo-container {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}

.todo-column, .progress-column, .done-column {
  border: 1px solid $gray;
  width: 350px;
  margin: 0 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.todo-column__head, .progress-column__head, .done-column__head {
  position: absolute;
  top: 0;
  width: 348px;
  height: 50px;
  border: 1px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;

}

.todo-column__head {
  background-color: $light-blue;
}

.progress-column__head {
  background-color: $light-orange;
}
.done-column__head {
  background-color: $light-green;
}

.todo-column__tasks, .progress-column_tasks, .done-column__tasks {
  min-height: 370px;
  overflow-y: scroll;
}

.btn:focus {
  box-shadow: none !important;
}

.btn-primary, .btn-success {
  position: absolute;
  bottom: 0;
  width: 348px;
  height: 48px;
  border: none !important;
}

.btn-primary {
  background-color: $light-blue !important;
}

.btn-primary:hover {
  background-color: $light-blue-hover !important;
}

.btn-success {
  background-color: $light-green !important;
}

.btn-success:hover {
  background-color: $light-green-hover !important;
}


//Modal

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: $background;
}

.modal-content {
  background-color: #fefefe;
  margin: 100px auto;
  border: 1px solid #888;
  width: 500px !important;
  height: 350px;
  padding: 30px;
}

.title_input {
  width: 260px;
  height: 50px;
  margin-bottom: 15px;
  border: 1px solid $gray;
  outline: none;
  padding-left: 10px;
}

.description_input {
  padding-left: 10px;
  padding-top: 5px;
}

textarea {
  outline: none;
}
.modal_btns {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.btn-danger, .btn-warning {
  width: 85px;
  border: none !important;
}

.btn-danger {
  margin-left: 110px;
  background-color: $pink !important;
}
.btn-warning {
  background-color: $grass !important;
}

.btn-secondary {
  background-color: $lavender !important;
  border: none !important;
  height: 40px;
}


// TODO Template

.todo {
  width: 330px;
  min-height: 180px;
  position: relative;
  border: 1px solid $gray;
  display: flex;
  flex-direction: column;
  padding: 12px;
}

.header-todo {
  display: grid;
  grid-template-columns: 3fr 1fr 1fr;
  padding-bottom: 10px;
}

.title-todo {
  margin-top: 10px;
  font-size: 18px;
  font-weight: bold;
}

.description-todo {
  min-height: 50px;
  word-wrap: break-word;
  font-size: 16px;
}

.footer-todo {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  font-size: 16px;
}

.user-todo, .time-todo {
  font-size: 14px;
}

.btn-outline-secondary, .btn-outline-danger {
  font-size: 14px !important;
  height: 32px;
  margin-left: 7px;
}

.btn-ou5line-secondary {
  border: 1px solid $light-gray !important;
}

.btn-outline-secondary:hover {
  background: $light-gray !important;
}

.btn-outline-danger {
  border: 1px solid $pink !important;
}

.btn-outline-danger:hover {
  background: $pink !important;
}




// TODO DRAG & DROP
.todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  width: 348px;
  top: 50px;
  background-color: #fff;
  box-shadow: rgba(15, 15, 15, 0.1) 0 0 0 1px,
  rgba(15, 15, 15, 0.1) 0 2px 4px;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
  margin: 0.5rem 0;
}

.todo .close {
  right: 1rem;
  top: 0;
  font-size: 2rem;
  color: #ccc;
  cursor: pointer;
}

.todo .close:hover {
  color: #343444;
}